import { _decorator, Component, Node, find } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('talkManeger')
export class talkManeger extends Component {
    public message:Array<string> = [
        "神木使者，你终于来了……敌族火族窥伺我族<神木>已久，得不到就想销毁它。派出了火族的大军正在来的路上。",
        "还好使者来的及时，只有使者才能驱动神木之力，建筑塔防，拯救神木。",
        "我给你介绍一下塔防的建筑吧。",
        "这是散射炮，可以朝着敌军的方向发射子弹",
        "这是追踪弹，可以锁定目标敌机执行打击",
        "这是光子箭，可以发射光子束打击敌军",
        "拖动这些建筑就可以生效啦。",
        "这是扩建功能,去建造更多的<神木卫阵>吧",
        "这是召唤风翼军，风翼军是个很强的兵种",
        "这是召唤卫木精灵"
    ]
    start() {
        this.node.on(Node.EventType.MOUSE_DOWN,this.talkContinue,this)
    }

    update(deltaTime: number) {
        
    }

    talkContinue(){
        find("Canvas/talk/message")
    }
}


